require("../../../common/api.js");
const commom = require("../../../common/common.js");

const YNZapi = commom.YNZapi;
const YNZ = commom.YNZ;
var openid = commom.getCookie('openid');
$(document).ready(function () {
    /**/
    $.ajax({
        headers:{'session': openid},
        type:'GET',
        url:YNZapi+"/Areas/0",
        dataType:'json',
        success:function (data) {
            if(data.code == 0){
                var result =data.result;
                console.log(result);
                for(var i in result){
                    var areaId = result[i]['areaId']; //ID
                    var areaName = result[i]['areaName']; //
                    var parentId = result[i]['parentId']; //ID

                    var template = `<option value="${areaId}">- ${areaName} -</option>`;
                    $(".province").append(template);
                }
            }
        }
    });
    /**/
    $(".province").change(function () {
        var areaId =$(".province").val();
        changeCity(areaId);
    });


    /**/
    function changeCity(areaId) {
        $(".city").empty();
        $.ajax({
            headers:{'session': openid},
            type:'GET',
            url:YNZapi+"/Areas/"+areaId,
            dataType:'json',
            success:function (data) {
                if(data.code == 0){
                    var result =data.result;
                    console.log(result);
                    for(var i in result){
                        var areaId = result[i]['areaId']; //ID
                        var areaName = result[i]['areaName']; //
                        var parentId = result[i]['parentId']; //ID

                        var template = `<option value="${areaId}">- ${areaName} -</option>`;
                        $(".city").append(template);
                        changeDistrict(areaId);
                    }
                }
            }
        });
    }

    /**/
    $(".city").change(function () {
        var areaId =$(".city").val();
        changeDistrict(areaId);
    });

    /**/
    function changeDistrict(areaId) {
        $(".district").empty();
        $.ajax({
            headers:{'session': openid},
            type:'GET',
            url:YNZapi+"/Areas/"+areaId,
            dataType:'json',
            success:function (data) {
                if(data.code == 0){
                    var result =data.result;
                    console.log(result);
                    for(var i in result){
                        var areaId = result[i]['areaId']; //ID
                        var areaName = result[i]['areaName']; //
                        var parentId = result[i]['parentId']; //ID

                        var template = `<option value="${areaId}">- ${areaName} -</option>`;
                        $(".district").append(template);
                    }
                }
            }
        });
    }

    /**/
    $("#footer").on('click',function (e) {
        e.preventDefault();
        var name = $(".name").val();/**/
        var phone = $(".phone").val();/**/
        var address = $(".address").val();/**/
        var areaId = $(".district").val();/**/
        if ($(".defaultBtn").is(":checked")){   /**/
            var defaultBtn = 1 ;
        }else {var defaultBtn = 0;}

        /**/
        if(!(/^[1][3-8]+\d{9}$/.test(phone))){
            alert("");
            $("#phone").focus();
            return false;
        }
        if(!name){
            alert("");
            return false;
        }
        if(!address){
            alert("");
            return false;
        }

        /**/
        var data={};
        data['userName'] = name ;
        data['userPhone'] = phone ;
        data['userAddress'] = address ;
        data['isDefault'] = defaultBtn ;
        data['areaId'] = areaId;

        $.ajax({
            headers:{'session': openid},
            type:'POST',
            url:YNZapi+'/address',
            dataType:'json',
            data:data,
            success:function (data) {
                if (data.code == 0){
                    alert("");
                    history.back();
                }
            }
        })
    })
});


